.br-theme-bars-1to10 {
	.br-widget {
		height: 50px;
		white-space: nowrap;
		a {
			display: block;
			width: 12px;
			padding: 5px 0;
			height: 28px;
			float: left;
			margin: 1px;
			text-align: center;
		}
		.br-current-rating {
			font-size: 20px;
			line-height: 2;
			float: left;
			padding: 0 20px 0 20px;
			font-weight: 400;
		}
	}
	.br-readonly a {
		cursor: default;
	}
}
@media print {
	.br-theme-bars-1to10 .br-widget {
		a {
			border: 1px solid #b3b3b3;
			background: white;
			height: 38px;
			-webkit-box-sizing: border-box;
			-moz-box-sizing: border-box;
			box-sizing: border-box;
			&.br-active, &.br-selected {
				border: 1px solid black;
				background: white;
			}
		}
		.br-current-rating {
			color: black;
		}
	}
}
